<template>
  <div id="detailable-footer">
    <ul class="post-copyright">
      <li class="post-copyright-author">
        <strong></strong>
        <a href="/"> {{ site.author }} </a>
      </li>

      <li class="post-copyright-title">
        <strong></strong>
        <a href="#">{{ target.title }}</a>
      </li>

      <li class="post-copyright-link">
        <strong></strong>
        <a href="#">{{ link }}</a>
      </li>

      <li class="post-copyright-date">
        <strong></strong>{{ target.date|format("YYYYMD - HHMM") }}
      </li>

      <li class="post-copyright-license">
        <strong> </strong>
        <a href="https://creativecommons.org/licenses/by-nc-nd/3.0/deed.zh" rel="license"
                            target="_blank"> ---3.0</a>
      </li>
    </ul>
  </div>
</template>

<script>
  import {Article, Page} from "@/models/article.class";
  import {Site} from "@/models/hexo-config.class";
  import {RootState} from "@/store";
  import {router} from "@/router";

  export default {
    name: "detailable-footer",
    props: {
      target: {
        required: true,
        validator: obj => obj instanceof Article || obj instanceof Page
      },
    },
    data() {
      return {
        // title: this.target.title,
        site: this.$root.$store.state.meta.hexoConfig.site,
        link: window.location.href
      }
    },
    methods: {
      showParentData() {
        console.info(this.target);
        console.info(this.site);
        console.info(this.$route.path);
        console.info(window);
      },
      getURL(url) {
        console.info(url)
        let strURL = "";
        if (url.substr(0, 7).toLowerCase() == "http://" || url.substr(0, 8).toLowerCase() == "https://") {
          strURL = url;
        } else {
          strURL = "http://" + url;
        }
        return strURL;
      }

    },
    created() {
      // this.showParentData();
    }
  }
</script>

<style lang="scss">
  @import '../../../styles/vars.scss';

  .post-copyright {
    margin: 2em 0 0;
    padding: 0.5em 1em;
    border-left: 3px solid #FF1700;
    background-color: #F9F9F9;
    list-style: none;
    text-align: left;
    line-height: 1.6rem;
  }
</style>





